.caleg {
  padding: 6rem;
  display: flex;
  gap: 5em;
  height: 100%;
  background-color: var(--white);
}

.card {
  /* padding: 2em; */
  background-color: white;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  width: 15rem;
  /* gap: 1em; */
  box-shadow: 0 0 2em var(--shadow-dark);
}

.card > img {
  max-height: 15rem;
  object-fit: cover;
  border-radius: 2em;
  width: 100%;
}

.card > .nomer {
  background-color: var(--blue);
  color: var(--white);
  position: relative;
  display: flex;
  width: fit-content;
  margin: 1em 0;
  justify-content: center;
  transform: translateX(-1em);
  border-radius: 1rem;
  font-weight: bold;
  font-size: 1.25em;
  padding: 0.75rem;
  display: flex;
}

.card > .nama {
  display: flex;
  justify-content: center;
  font-size: 1.75em;
  color: var(--black);
  font-weight: bold;
  margin-bottom: 1em;
  text-transform: uppercase;
}
