.sejarah {
  padding: 6rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3em;
}

.sejarah > img {
  max-width: 10em;
  filter: drop-shadow(0.5em 0.5em 1em var(--shadow-dark));
}

.sejarah > h1 {
  font-size: 2.5em;
  color: var(--blue);
}

.sejarah > p {
  font-size: 1.75em;
  color: var(--black);
}
