.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2em 4em;
}
a {
  text-decoration: none;
}
.logo {
  margin-right: 5em;
}
.logo img {
  max-height: 4em;
}
.link {
  display: flex;
  align-items: center;
  max-height: 100px;
  list-style: none;
  font-weight: bold;
  color: var(--white-min);
  font-size: 1.25em;
}
.link .active {
  color: var(--white);
  border-bottom: 0.2em solid var(--white);
}
.link .link-txt:hover {
  color: var(--red);
  cursor: pointer;
  border-bottom: 0.2em solid var(--red);
}
.link .link-txt {
  color: var(--white);
  margin: 0 1em;
  padding: 1em 0;
  transition: 0.2s;
}
.login {
  display: flex;
  justify-content: center;
  height: 100%;
  align-content: center;
}
.login .button {
  border: none;
  border-radius: 0.75em;
  padding: 0.5em 1em;
  margin: 0;
  background-color: var(--white);
  font-weight: bold;
  font-size: 1.25em;
  color: var(--blue);
  transition: 0.2s;
}
.login .button:hover {
  background-color: var(--white-blue);
  cursor: pointer;
  box-shadow: 0.5em 0.5em 3em var(--shadow-light);
  color: var(--black-blue);
}
.dropdown {
  position: relative;
}
.popup {
  position: absolute;
  background-color: var(--white);
  border-radius: 0.5em;
  color: var(--black-blue);
  padding: 0.5em;
  box-shadow: 1em 1em 4em var(--shadow-dark);
  animation: popup 0.2s;
}
.popup-link {
  padding: 1em 2em;
  border-radius: 0.5em;
  transition: 0.1s;
  color: var(--black-blue);
}
.popup-link:hover {
  background-color: var(--white-blue);
  cursor: pointer;
}

@keyframes popup {
  0% {
    transform: translateY(-5em);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
  }
}
